<template>
  <div>
    <div id="map"></div>
    <a id="back-home" class="back-button" v-if="backBtn" @click="back">
      返回
    </a>
  </div>
</template>
<script>

import axios from "axios";
export default {
  name: "index",
  data() {
    return {
      map: {},
      backBtn: false,
      mapHistory:[],
      length: 0,
      maxLength: 1
    };
  },
  created(){
    this.getWorldJson()
    this.mapHistory.push()
  },
  mounted(){},
  methods: {
    async getWorldJson(){
      let {data:geoJson} = await axios.get('./countries.geo.json')
      this.worldMap(geoJson)
    },
    worldMap(geoJson){
      this.$echarts.registerMap("world", geoJson);
      let myChart = this.$echarts.init(document.getElementById("map"));
      let setOption = {
      title: {
        subtext: "",
        left: "center",
        top: "3%",
        textStyle: {          
          color: "#d5e7f3",
          fontSize: 18,
          fontFamily:'微软雅黑',
          fontWeight:'bold'
        },
      },
      //提示框
      tooltip: {
        // formatter: function(params) {
        //   return params.name;
        // },
      },
      geo: {
        map: "world",
        show: true,
        //是否开启鼠标缩放和平移漫游
        roam: false,
        left: "center",
        select: {
          label: {
            show: true,
            color: "#fff",
          },
        },
        itemStyle: {
          normal: {
            areaColor: "#3a7fd5",
            borderColor: "#0a53e9",
            shadowColor: "#092f8f",
            shadowBlur: 20,
          },
          emphasis: {
            areaColor: "#0a2dae",
          },
        },
      },
      series: [
        {
          type: "map",
          map: "HZ",
          geoIndex: 0,
        },
        {
          name: "pin",
          type: "scatter",
          coordinateSystem: "geo",
          symbol: "pin",
          symbolSize: [50, 50],
          label: {
            normal: {
              show: true,
              textStyle: {
                color: "#fff",
                fontSize: 9,
              },
            },
          },
          itemStyle: {
            normal: {
              color: "#ffcc00",
            },
          },
          showEffectOn: "render",
          rippleEffect: {
            brushType: "stroke",
          },
          hoverAnimation: true,
          zlevel: 1,
        },
      ],
      }
      myChart.setOption(setOption,true);
      window.addEventListener("resize", function() {
        myChart.resize();
      });

      myChart.on('click',async(data)=>{
        let name
        name = data.name
        let {data:compJson} = await axios.get('./comp.json')
        for(let i = 0;i<compJson.array.length;i++){
          if(name == compJson.array[i].name){
            var newI = compJson.array[i].json
            var newAddr = `./countries/`+newI+`.geo.json`
            let {data:newJson} = await axios.get(newAddr)
            this.$echarts.registerMap("world", newJson);
            myChart.setOption(setOption);
            //这里有逻辑错误，因为目前是下钻只有一个，所以先这么用
            if(this.length<this.maxLength){
              this.mapHistory.push(name)
              this.length+=1
            }
            this.backBtn = true 
          }
        }
      })


    },
    async back(){
      
      //判断长度
      if(this.length>0){
        //先踢出去一个
        this.mapHistory.pop()
        this.length --
      }

      if(this.length == 0){
        this.backBtn = false
      }
      
      if(this.mapHistory.length < 1){
        this.getWorldJson()
      }
      else{
        let historyCityName = this.mapHistory[this.mapHistory.length -1]
        let { data:MapJson } = await axios.get(`./countries/`+historyCityName+`.geo.json`)
        this.worldMap(MapJson)
      }
    }
  },
};
</script>

<style scoped>
@import "../styles/map.css";
</style>
